gallery container-md\">\n            <div>\n                <div\n                    @click=\"\n                        activeGallery = 'Pictures';\n                        clickedAlbum = [];\n                    \"\n                    :class=\"{\n                        'wed-host-gallery-active': activeGallery == 'Pictures',\n                    }\"\n                >\n                    Pictures\n                </div>\n                <div\n                    @click=\"\n                        activeGallery = 'Albums';\n                        clickedAlbum = [];\n                    \"\n                    :class=\"{\n                        'wed-host-gallery-active': activeGallery == 'Albums',\n                    }\"\n                >\n                    Albums\n                </div>\n                <div\n                    @click=\"\n                        activeGallery = 'Videos';\n                        clickedAlbum = [];\n                    \"\n                    :class=\"{\n                        'wed-host-gallery-active': activeGallery == 'Videos',\n                    }\"\n                >\n                    Videos\n                </div>\n            </div>\n            <div class=\"wed-host-gallery-items\">\n                <div v-if=\"activeGallery == 'Pictures'\">\n                    <form\n                        @submit.prevent=\"sendPictures($event)\"\n                        class=\"wed-image-form gallery_upload_form\"\n                    >\n                        <div class=\"p-3\">\n                            <div class=\"row p-2\">\n                                <label for=\"album\" class=\"form-label\">\n                                    Album Name</label\n                                >\n                                <input\n                                    type=\"text\"\n                                    name=\"album\"\n                                    class=\"form-control\"\n                                    v-model=\"albumName\"\n                                    required\n                                />\n                            </div>\n                            <div class=\"row mb-2 p-2\">\n                                <label for=\"image\" class=\"form-label\"\n                                    >Add Images</label\n                                >\n                                <input\n                                    type=\"file\"\n                                    name=\"galleryImage[]\"\n                                    class=\"form-control form-control-sm\"\n                                    id=\"\"\n                                    multiple\n                                    @change=\"uploadImages($event)\"\n                                />\n                                <span\n                                    v-if=\"\n                                        errorsSubmit &&\n                                        errorsSubmit.galleryImage\n                                    \"\n                                    class=\"errMsg\"\n                                    >{{ errorsSubmit.galleryImage[0] }}</span\n                                >\n                                <div>{{ uploadMultiMessage }}</div>\n                            </div>\n                            <div class=\"row p-3\">\n                                <button\n                                    type=\"submit\"\n                                    class=\"btn btn-sm btn-primary\"\n                                    :disabled=\"disablePictures\"\n                                >\n                                    Create Album\n                                </button>\n                            </div>\n                        </div>\n                    </form>\n                    <div\n                        v-for=\"(item, index) in allGalleries\"\n                        :key=\"index\"\n                        class=\"img-cont\"\n                    >\n                        <img\n                            :src=\"\n                                '/files/' +\n                                invitations.id +\n                                '/' +\n                                item.imageName\n                            \"\n                            alt=\"\"\n                        />\n                    </div>\n                </div>\n                <div\n                    id=\"all-albums\"\n                    v-if=\"activeGallery == 'Albums' && clickedAlbum.length == 0\"\n                >\n                    <div\n                        v-for=\"(item, index) in allAlbums\"\n                        :key=\"index\"\n                        @click=\"clickedAlbum = item.images\"\n                        class=\"album-cont\"\n                    >\n                        <span>\n                            {{ item.images.length }}\n                            <img src=\"/assets/guestInvi/bi_image-fill.svg\" />\n                        </span>\n                        <img src=\"/assets/guestInvi/Frame-5602-1.png\" alt=\"\" />\n                        <span> {{ item.name }} </span>\n                    </div>\n                </div>\n                <div v-else class=\"album-pics\">\n                    <div\n                        v-for=\"single in clickedAlbum\"\n                        :key=\"single.imageName\"\n                        class=\"img-cont\"\n                    >\n                        <img\n                            :src=\"\n                                '/files/' +\n                                invitations.id +\n                                '/' +\n                                single.imageName\n                            \"\n                            alt=\"\"\n                        />\n                    </div>\n                </div>\n                <div v-if=\"activeGallery == 'Videos'\">\n                    <form\n                        class=\"wed-image-form gallery_upload_form\"\n                        @submit.prevent=\"sendPictures($event)\"\n                    >\n                        <div class=\"p-3\">\n                            <div class=\"row mb-2 p-2\">\n                                <label for=\"image\" class=\"form-label\"\n                                    >Add Video</label\n                                >\n                                <input\n                                    type=\"file\"\n                                    name=\"galleryImage[]\"\n                                    class=\"form-control form-control-sm\"\n                                    id=\"\"\n                                    multiple\n                                    @change=\"uploadImages($event)\"\n                                />\n                                <span\n                                    v-if=\"\n                                        errorsSubmit &&\n                                        errorsSubmit.galleryVideo\n                                    \"\n                                    class=\"errMsg\"\n                                    >{{ errorsSubmit.galleryVideo[0] }}</span\n                                >\n                                <div>{{ uploadMultiMessage }}</div>\n                            </div>\n                            <div class=\"row p-3\">\n                                <button\n                                    :disabled=\"disablePictures\"\n                                    class=\"btn btn-sm btn-primary\"\n                                >\n                                    Upload Video\n                                </button>\n                            </div>\n                        </div>\n                    </form>\n                    <div\n                        v-for=\"(item, index) in allVideos\"\n                        :key=\"index\"\n                        class=\"img-cont\"\n                    >\n                        <video controls>\n                            <source\n                                :src=\"\n                                    '/videos/' +\n                                    invitations.id +\n                                    '/' +\n                                    item.name\n                                \"\n                            />\n                            Your browser does not support the video tag.\n                        </video>\n                    </div>\n                </div>\n            </div>\n        </div>\n        <flashMessage :message=\"message\"></flashMessage>\n    </div>\n</template>\n\n<script>\nimport flashMessage from \"../FlashMessage.vue\";\n\nexport default {\n    components: {\n        flashMessage,\n    },\n    props: [\"albums\", \"galleries\", \"videos\", \"host\", \"invitations\"],\n    data() {\n        return {\n            activeGallery: \"Pictures\",\n            clickedAlbum: [],\n            message: null,\n            uploadMultiImage: [],\n            uploadMultiMessage: null,\n            albumName: null,\n            allAlbums: this.albums,\n            allGalleries: this.galleries,\n            allVideos: this.videos,\n            disablePictures: false,\n            errorsSubmit: null,\n        };\n    },\n    methods: {\n        uploadImages(e) {\n            this.uploadMultiImage = e.target.files;\n            this.uploadMultiMessage =\n                this.uploadMultiImage.length + \" Files Upoladed!!\";\n        },\n        sendPictures(e) {\n            let _this = this;\n            _this.disablePictures = true;\n            _this.errorsSubmit = null;\n            let formData = new FormData();\n            let link = null;\n            if (_this.activeGallery == \"Pictures\") {\n                formData.append(\"album\", _this.albumName);\n                let images = null;\n                if (_this.uploadMultiImage.length > 0) {\n                    images = [..._this.uploadMultiImage];\n                    images.forEach(function (item, index) {\n                        formData.append(\"galleryImage[\" + index + \"]\", item);\n                    });\n                }\n                link = route(\"addAlbum\", _this.host);\n            }\n            if (_this.activeGallery == \"Videos\") {\n                let images = null;\n                if (_this.uploadMultiImage.length > 0) {\n                    images = [..._this.uploadMultiImage];\n                    images.forEach(function (item, index) {\n                        formData.append(\"galleryVideo[\" + index + \"]\", item);\n                    });\n                }\n                link = route(\"hostvideoUpload\", _this.host);\n            }\n\n            let meth = \"POST\";\n\n            axios({\n                method: meth,\n                url: link,\n                data: formData,\n                headers: { \"content-type\": \"multipart/form-data\" },\n            })\n                .then((response) => {\n                    _this.allAlbums = response.data[0];\n                    _this.allGalleries = response.data[1];\n                    _this.allVideos = response.data[2];\n                    _this.message = \"Uploaded!!!\";\n                    _this.uploadMultiMessage = null;\n                    _this.uploadMultiImage = [];\n                    _this.disablePictures = false;\n                    setTimeout(function () {\n                        _this.message = null;\n                    }, 3000);\n                })\n                .catch((error) => {\n                    _this.disablePictures = false;\n                    if (_this.activeGallery == \"Pictures\") {\n                        _this.errorsSubmit = {\n                            galleryImage: Object.values(\n                                error.response.data?.errors\n                            )[0],\n                        };\n                    }\n                    if (_this.activeGallery == \"Videos\") {\n                        _this.errorsSubmit = {\n                            galleryVideo: Object.values(\n                                error.response.data?.errors\n                            )[0],\n                        };\n                    }\n                });\n        },\n    },\n};\n</script>\n\n<style scoped>\n/* Gallery all css start */\n.wed-host-gallery {\n    background-color: white;\n    margin-top: 2em;\n    padding: 10px 0;\n}\n\n.wed-host-gallery > div:nth-child(1) {\n    display: flex;\n    justify-content: center;\n}\n\n.wed-host-gallery-active {\n    font-weight: bold !important;\n    color: #7f004b !important;\n    border-bottom: 4px solid #7f004b !important;\n}\n\n.wed-host-gallery > div:nth-child(1) > div {\n    font-family: Roboto;\n    font-style: normal;\n    font-weight: normal;\n    font-size: 17.8211px;\n    line-height: 21px;\n    color: #968585;\n    flex: 1;\n    text-align: center;\n    padding: 2em 0;\n    border-right: 0.891053px solid #cbc7c7;\n    border-bottom: 0.891053px solid #cbc7c7;\n    cursor: pointer;\n}\n.wed-host-gallery > div:nth-child(1) > div:last-child {\n    border-right: none;\n}\n.wed-host-gallery-items {\n    padding-bottom: 1em;\n}\n.wed-host-gallery-items > div {\n    display: grid;\n    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));\n    grid-auto-rows: max-content;\n    padding: 1em;\n    grid-column-gap: 1em;\n    grid-row-gap: 1.5em;\n}\n.img-cont,\n.album-cont {\n    position: relative;\n    padding-top: 100%;\n    border-radius: 20px;\n}\n.img-cont > img,\n.img-cont > video,\n.album-cont > img {\n    position: absolute;\n    top: 0;\n    width: 100%;\n    height: 100%;\n    object-fit: cover;\n    border-radius: 20px;\n    z-index: 99;\n}\n.album-cont > span:nth-child(1) {\n    position: absolute;\n    top: 15px;\n    right: 15px;\n    z-index: 999;\n    font-family: \"Roboto\";\n    font-style: normal;\n    font-weight: 400;\n    font-size: 16px;\n    line-height: 19px;\n    color: #ffffff;\n}\n.album-cont > span:nth-child(3) {\n    position: absolute;\n    bottom: 15px;\n    left: 15px;\n    z-index: 999;\n    font-family: \"Roboto\";\n    font-style: normal;\n    font-weight: 500;\n    font-size: 15px;\n    line-height: 18px;\n    color: #ffffff;\n}\n.album-pics {\n    /* display: none;\n    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));\n    grid-auto-rows: max-content;\n    padding: 1em;\n    grid-column-gap: 1em;\n    grid-row-gap: 1.5em; */\n}\n/* .album-pics.active {\n    display: grid !important;\n}\n.album-cont.active {\n    display: none !important;\n} */\n\n/* Gallery all css end */\n.wed-image-form {\n    grid-column-start: 1;\n    grid-column-end: 3;\n}\n\n.gallery_upload_form {\n    background: rgba(226, 226, 226, 0.6);\n    backdrop-filter: blur(4px);\n    border-radius: 17.8211px;\n    height: max-content;\n}\n\n.gallery_album_form {\n    background: rgba(226, 226, 226, 0.6);\n    backdrop-filter: blur(4px);\n    border-radius: 17.8211px;\n}\n\n/* .gallery_upload_form>input {\n            display:none;\n        } */\n\n.gallery_upload_form1 > label {\n    display: flex;\n    justify-content: center;\n    width: 100%;\n    height: 100%;\n    cursor: pointer;\n    flex-direction: column;\n}\n\n.gallery_upload_form1 > label > span {\n    align-self: center;\n}\n\n.gallery_upload_form > label {\n    display: flex;\n    justify-content: center;\n    width: 100%;\n    height: 100%;\n    cursor: pointer;\n    flex-direction: column;\n}\n\n.gallery_upload_form > label > span {\n    align-self: center;\n}\n@media screen and (max-width: 769px) {\n    .wed-host-gallery {\n        padding: 14px 0 !important;\n    }\n}\n@media screen and (max-width: 576px) {\n    .wed-host-gallery-items > div {\n        display: grid;\n        grid-template-columns: repeat(2, 1fr) !important;\n        grid-auto-rows: max-content;\n        padding: 1em;\n        grid-column-gap: 1em;\n        grid-row-gap: 1.5em;\n    }\n}\n</style>\n"],"sourceRoot":""}]);
